body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mainLayout {
    display: grid;
    grid-template-columns: auto 1fr 1fr auto;
    grid-template-rows: auto;
    grid-template-areas: "header header header header" "main main main sidebar" "footer footer footer footer";
}

.mainLayout>.sidebar {
    grid-area: sidebar;
}

.mainLayout>.header {
    grid-area: header;
}

.mainLayout>.footer {
    grid-area: header;
}

.mainLayout>.content {
    grid-area: main;
}
h1{
    font-size: 24px;
    padding: 4px;
}

h2{
    font-size: 20px;
    padding: 4px;
}

h3{
    font-size: 18px;
    padding: 4px;
}

.p-listbox{
    width: 20em;
}
.blog-list {}

.blog-post {
    margin: 10px;
    padding: 0;
    border: 4px solid #888888;
}

.blog-post-title {
    margin: 0;
    padding: 10px;
    background: #888888;
}

.blog-post-content {
    margin: 0;
    padding: 10px;
}

.blog-post-content img {
    max-width: 100%;
}

.blog-post-content-collapsed {
    /* margin: 0 0 -60px 0; */
    max-height: 300px;
    overflow: hidden;
}

.blog-post-more {
    height: 100px;
    margin: -60px 0 0 0;
    padding: 60px 0 0 0;
    font-size: 20px;
    text-align: center;
    box-shadow: inset 0px -50px 70px -25px rgba(0, 0, 0, 0.75);
}

.p-toggleable-content-collapsed {
    display: unset;
}

body .p-accordion .p-toggleable-content-collapsed>.p-accordion-content {
    max-height: 140px;
    overflow: hidden;
    box-shadow: inset 0px -50px 50px 0px rgba(0, 0, 0, 0.75);
}
